





















































































































































































































































$flex_width: 70%;
.step_content {
    margin: 90px auto;
    .form_box {
        display: flex;
        justify-content: center;
        width: auto;
        text-align: center;
    }
    .el-form-item__label {
        color: #333333;
    }
}
.step_content {
    display: flex;
    justify-content: center;
    width: $flex_width;
    margin: 104px auto;
    .el-form {
        .item_txt {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #666666;
        }
    }
    // 第二个
    .step_title {
        width: 100%;
        span {
            font-size: 18px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: #333333;
        }
    }
}

.step_box {
    margin: 64px auto;
    display: flex;
    color: #cccccc;
    align-items: center;
    justify-content: center;

    .step_line {
        flex: 1;
        background: #cccccc;
        height: 2px;
    }
    .step_item.isActive {
        .step_circle {
            border: 1px solid transparent;
            background: rgba(251, 71, 2, 1);
            span {
                font-size: 14px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: #ffffff;
            }
        }
        .step_txt span {
            color: #333;
            font-weight: 500;
        }
    }
    .step_item {
        cursor: pointer;
        display: flex;
        align-items: center;
        padding: 0 5px;
        .step_circle {
            width: 31px;
            height: 31px;
            line-height: 31px;
            border-radius: 50%;
            text-align: center;
            border: 1px solid rgba(0, 0, 0, 0.25);
            background: #ffffff;
            margin: 0 12px;
            font-size: 14px;
            span {
                font-size: 14px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: rgba(0, 0, 0, 0.45);
            }
        }
        .step_txt span {
            font-weight: 400;
            font-family: PingFangSC-Medium, PingFang SC;
            color: rgba(0, 0, 0, 0.45);
        }
    }

    .btn_submit {
        cursor: pointer;
        width: 256px;
        height: 38px;
        text-align: center;
        line-height: 38px;
        background: #fb4702;
        border-radius: 4px;
        span {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #ffffff;
        }
    }
}
.set_box {
    width: 100%;
    .set_header {
        width: 70%;
        margin: 64px auto;
    }
    .el-step.is-horizontal {
        .el-step__head.is-success {
            color: #fb4702 !important;
            border-color: #fb4702 !important;
        }
        .el-step__head {
            .is-process {
            }
            .el-step__icon {
                position: relative;
                z-index: 1;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                width: 32px;
                height: 32px;
                font-size: 14px;
                box-sizing: border-box;
                background: #fff;
                transition: 0.15s ease-out;
            }
            .el-step__line {
                top: 16px !important;
            }
        }
        .el-step__main {
            width: 100px;
            position: relative;
            top: -34px;
            left: 0;
            background: #fff;
            padding-left: 34px;
            .el-step__title {
                font-size: 16px;
                font-family: PingFangSC-Medium, PingFang SC;
                font-weight: 500;
            }
            .el-step__title.is-success {
                color: #000;
            }
        }
    }
}
